.g-tab-cont .total:before, .g-tab-cont .total:after { content: ""; display: table; }

.g-tab-cont .total:after { clear: both; overflow: hidden; }

.g-tab-cont .total .lt { float: left; font-size: 0.48rem; }

.g-tab-cont .total .lt span { color: #fa4b9b; }

.g-tab-cont .total .rt { float: right; font-size: 0; }

.g-tab-cont .total .rt a { display: inline-block; vertical-align: top; margin-left: 0.26667rem; padding: 0 0.18667rem; height: 0.74667rem; line-height: 0.74667rem; border-radius: 0.1rem; border: 1px solid #d7d7d7; font-size: 0.37333rem; color: #333; text-align: center; }

/*! 订单 */
.g-order .info dl { padding: 0.26667rem; border-bottom: 1px solid #ebebeb; font-size: 0.37333rem; }

.g-order .info dl:last-child { border-bottom: none; }

.g-order .info dt { margin-bottom: 2px; }

.g-order .info dd { color: #999; }

.g-order .total .tit { padding: 0 0.26667rem; line-height: 1.06667rem; font-weight: normal; font-size: 0.26667rem; border-bottom: 1px solid #d7d7d7; }

.g-order .total .g-item { border-bottom: 1px solid #d7d7d7; }

.g-order .row dl { *zoom: 1; padding: 0 0.26667rem; height: 1.06667rem; line-height: 1.06667rem; border-bottom: 1px solid #d7d7d7; }

.g-order .row dl:before, .g-order .row dl:after { content: ""; display: table; }

.g-order .row dl:after { clear: both; overflow: hidden; }

.g-order .row dl:last-child { border-bottom: none; }

.g-order .row dt { float: left; color: #999; }

.g-order .row dd { float: right; }

/*! 弹层 */
.popBox { position: fixed; top: 0; left: 0; right: 0; bottom: 0; background: rgba(0, 0, 0, 0.7); z-index: 1000; display: -webkit-box; display: -moz-box; display: -ms-flexbox; display: -webkit-flex; display: flex; -webkit-box-pack: center; -moz-box-pack: center; -ms-flex-pack: center; -webkit-justify-content: center; justify-content: center; -webkit-box-align: center; -moz-box-align: center; -ms-flex-align: center; -webkit-align-items: center; align-items: center; }

.pop { position: relative; padding: 0.4rem; width: 7.46667rem; height: 6rem; background-color: #fff; border-radius: 0.26667rem; font-size: 0.4rem; }

.pop .close { position: absolute; left: 50%; bottom: -1.33333rem; margin-left: -0.5rem; width: 1rem; height: 1rem; background: url("../images/icon_cancel.png") no-repeat center; background-size: 0.58667rem auto; }

.pop-form dt { margin-bottom: 0.26667rem; font-size: 0.4rem; }

.pop-form dd { *zoom: 1; margin-bottom: 0.26667rem; }

.pop-form dd:before, .pop-form dd:after { content: ""; display: table; }

.pop-form dd:after { clear: both; overflow: hidden; }

.pop-form .ipt-tel, .pop-form .ipt-yzm { display: block; padding: 0.25333rem 0; background-color: #f6f6f6; border-radius: 0.1rem; border: none; font: inherit; text-indent: 0.7em; }

.pop-form .ipt-tel { width: 100%; }

.pop-form .ipt-yzm { float: left; width: 3.73333rem; }

.pop-form .btn-yzm { float: right; border: none; width: 2.66667rem; height: 1.06667rem; background-color: #fa4b9b; border-radius: 0.1rem; color: #fff; }

.pop-form .btn-submit { margin-top: 0.4rem; border: none; width: 100%; height: 1.06667rem; background-color: #fa4b9b; border-radius: 0.1rem; color: #fff; }

.pop-success { text-align: center; }

.pop-success h1 { margin: 1.6rem 0 1.6rem 0; font-size: 0.48rem; color: #fa4b9b; }

.pop-success aside { font-size: 0.34667rem; }

.pop-success aside span { color: #fa4b9b; }
